@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';

.header{
    height: 80px;
    padding: 0 30px;
    background-color: $bgColor;
    @include flex(row, flex-end, center, nowrap);
    position: fixed;
    left: 0;
    right: 0;
    z-index: 9999;
    width: 100%;
    margin: 0 auto;
}
